import styled from 'styled-components';

export const UserSettingsWrapper = styled.div`
  width: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  overflow: scroll;
`;

export const SettingsHeaderCircle = styled.div`
  font-size: 25px;
  font-weight: 600;
  width: 1500px;
  height: 1500px;
  position: absolute;
  top: -1300px;
  border-radius: 100%;
  background: linear-gradient(to top left, #6f66ed, #ac7df7);
`;

export const SettingsHeader = styled.header`
  color: ${({ theme }) => theme.colors.lightGrey};
  font-size: 25px;
  z-index: 2;
  margin-top: 10%;
`;
